	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/frec_004-a9e33df1b08d1501f51bfaf930d7227d-d95e5.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 620px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.61290322580645%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"frec 004\"\n        title=\"\"\n        src=\"/static/frec_004-a9e33df1b08d1501f51bfaf930d7227d-d95e5.jpg\"\n        srcset=\"/static/frec_004-a9e33df1b08d1501f51bfaf930d7227d-63775.jpg 173w,\n/static/frec_004-a9e33df1b08d1501f51bfaf930d7227d-f2af4.jpg 345w,\n/static/frec_004-a9e33df1b08d1501f51bfaf930d7227d-d95e5.jpg 620w\"\n        sizes=\"(max-width: 620px) 100vw, 620px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Az FRT negyedik elektromos versenyautója, az FREC-004 kilenc hónap alatt közel nyolcvan csapattag több mint 31 ezer munkaórájának köszönhetően született meg. Az autó a 2013-ban nagy sikereket elérő Michaela továbbfejlesztése, amely kétéves projektnek indult, így drasztikus változások nem következtek be a váz, a hajtáslánc, a fékrendszer vagy éppen a felfüggesztés terén, inkább a finomításokon volt a hangsúly. A legszembetűnőbb változás a teljes aerodinamikai csomag újragondolása volt, hiszen az előző évben csak a diffúzor került beépítésre, a szárnyak továbbfejlesztésre szorultak. A következő évre azonban egy külön aerodinamika csoport alakult. Több száz szimuláció eredményeként készült el az új konstrukció, amely 72 km/h-nál 1000 N leszorító erő létrehozására képes. Ez közel 20%-os növekedést jelent a korábbi változathoz képest, miközben 10%-kal kisebb légellenállást generál. Ezek az értékek ráadásul tovább javíthatók a szárnyak állásszögének változtatásával, és 130 km/h-nál az autó képes a „plafonon menni”, azaz a leszorító erő ekkor több mint az autó tömegéből adódó súlyerő. A mérnökök jelentős fejlesztéseket végeztek az elektronika terén is, elsősorban az energiaellátó rendszerben és az intelligens hajtásszabályozásban.</p>","timeToRead":1,"excerpt":"Az FRT negyedik elektromos versenyautója, az FREC-004 kilenc hónap alatt közel nyolcvan csapattag több mint 31 ezer munkaórájának…","frontmatter":{"title":"FREC-004"},"fields":{"slug":"/frec-004"}}},"pathContext":{"slug":"/frec-004"}}

